/**
 * Vue的插件，用来获取和设置localStorage存储
 **/
let local = {
  save (key, value) {
    localStorage.setItem(key, JSON.stringify(value))
  },
  fetch (key) {
    return JSON.parse(localStorage.getItem(key)) || {}
  },
  del (key) {
    localStorage.removeItem(key)
  }
}

/**
 * Vue的插件，各种正则验证
 **/
let regEn = /[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im,          //英文特殊字符
    regCn = /[·！#￥（——）：；“”‘、，|《。》？、【】[\]]/im,  //中文特殊字符
    regZh = /[\u4e00-\u9fa5]/ig,                          //中文字符
    regCha = /[a-zA-Z]/ig,                                //英文字母
    regNum = /[0-9]/ig,                                   //数字

    publicMethod = {
      noSpecialCharacter (data) { // 若有特殊字符则返回true，若为false则只有数字字母
        return regEn.test(data) || regCn.test(data) || regZh.test(data)
      },
      chineseOnly (data) { // 若有字母和特殊字符则返回true，若为false则只有中文和数字
        return regCha.test(data) || regEn.test(data) || regCn.test(data)
      },
    }

export default {
  install: function (vm) {
    vm.prototype.$local = local;
    vm.prototype.$publicMethod = publicMethod
  }
}



