import Header from '@/components/header';
import { loginOut } from '@/lib/axios/api';

export default {
  data () {
    return {
      accountName: '',
    }
  },
  methods: {
    getAccount (data) {
      this.accountName = data;
    },
    userLoginOut () {
      loginOut('/SignIn/Logout')            // 退出登录状态，成功跳转至login页面
      .then((res) => {
        console.log('退出成功',res);
        if(res.status===204){
          this.$router.push('/login')
        } else {
          this.$Message.error('退出登录失败');
        }
      })
      .catch((err) => {
        console.log('退出失败',err);
        this.$Message.error('退出登录失败');
      })
    }
  },
  components: {
    headerNav: Header
  },
}



