import Vue from 'vue'
import Router from 'vue-router'

import Layout from '@/views/layout'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path:'/',
      // name:'Layout',
      component: Layout,
      children: [
        {
          path: '',  // 默认的子路由  /home 主页
          name: 'Home',
          component: resolve => require(['@/views/layout/home'],resolve)
        },
        {
          path: '/user/:sign',  // 用户管理
          name: 'User',
          component: resolve => require(['@/views/layout/user'],resolve)
        },
        {
          path: '/menu/:sign',  // 菜单管理
          name: 'Menu',
          component: resolve => require(['@/views/layout/menu'],resolve)
        }
      ]
    },
    {
      path:'/login',
      name:'login',
      component: resolve => require(['@/views/login'],resolve)
    },
    { //重定向至主页
      path: '*',
      redirect: '/'
    }
  ]
})



