// 中央事件总线插件

const install = function (Vue) {
  const Bus = new Vue ({
    methods : {
      emit (event, ...args) {         // 触发
        this.$emit(event, ...args);
      },
      on (event, callback) {          // 监听
        this.$on(event, callback);
      },
      off (event, callback) {         // 解除
        this.$off(event, callback);
      }
    }
  });
  Vue.prototype.$bus = Bus;
};

export default install;



