import { fetch } from "./fetch"; //引用fetch.js
import api from './url'; //引用url.js

//权限登录
export function login(url,data) {
  return fetch({
    //api.baseUrl 引用url.js里面的基础路径数据
    url: api.baseUrl + url,
    method: 'post',//请求方法
    data:data
  })
};

//当前账户下系统列表(home)
export function sysList(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'post',
    data:data
  })
};

//权限登录退出(home)
export function loginOut(url,data) {
  return fetch({
    //api.baseUrl 引用url.js里面的基础路径数据
    url: api.baseUrl + url,
    method: 'post',
    data:data
  })
};

/* 菜单子页面 --menu start-- */

//具体某一账户下菜单列表
export function menuList(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'get',
    params:data
  })
};

// 删除菜单
export function delMenu(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'delete',
    params:data
  })
};

// 新增/编辑菜单
export function saveMenu(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'post',
    data:data
  })
};

// 新增/编辑菜单处--根据菜单获取角色
export function getRolesByMenu(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'get',
    params:data
  })
};
/* 菜单子页面 --end-- */

