<template lang="html">
  <div class="headerContainer">
    <p class="headerTitle">统一权限管理系统</p>
    <Dropdown  @on-click="loginOut">
        <a href="javascript:void(0)" class="headerMes">
            <div class="headerPic">
              <img src="../assets/header.jpg" alt="">
            </div>
            <p>{{ account }}</p>
            <!-- <Icon type="ios-arrow-down"></Icon> -->
        </a>
        <DropdownMenu slot="list">
            <!-- <DropdownItem name="mes">信息</DropdownItem> -->
            <DropdownItem name="out">退出</DropdownItem>
        </DropdownMenu>
    </Dropdown>
  </div>
</template>

<style lang="css" scoped>
  @import './../style/layout.css';
</style>

<script>
  export default {
    data () {
      return {
        account: this.$local.fetch('Account')||"Admin"
      }
    },
    props: {
      accountName: String,        // 传入用户名字
    },
    methods: {
      loginOut (name) {
        if(name==="out"){
          this.$local.del('Account');
          this.$emit('loginOut');
        }else {
          console.log(name)
        }
      }
    },
    watch: {
      accountName () {         // 用于用户名传递
        this.account = this.accountName;
      }
    }
  }
</script>



